// CONTAINERS
// ==============================

$container-sizes: (
    'small'    : 768px,
    'large'    : 1440px,
) !default;

.container {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
}
.container-content{
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    flex-direction: column;
    height: 100%;
    width: 100%;

    .status{
        background: none;
        -webkit-box-shadow: 0 4px 5px 0 rgba(0,0,0,0.14), 0 1px 10px 0 rgba(0,0,0,0.12), 0 2px 4px -1px rgba(0,0,0,0.2);
        box-shadow: 0 4px 5px 0 rgba(0,0,0,0.14), 0 1px 10px 0 rgba(0,0,0,0.12), 0 2px 4px -1px rgba(0,0,0,0.2);
        -webkit-box-flex: 0 0 auto;
        -webkit-flex: 0 0 auto;
        flex: 0 0 auto;
        padding: 0;
        position: relative;
        z-index: 2;

        .s1{
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            display: -webkit-box;
            display: -webkit-flex;
            display: flex;
            font-stretch:100%;
            font-size: 21px;
            font-family: Roboto,RobotoDraft,Helvetica,Arial,sans-serif;
            display: flex;
            font-weight: 500;
            align-items: center;
            padding: 15px 30px;
            color: rgba(0,0,0,0.87);
            line-height: 25px;
        }
    }
    .loc{
        -webkit-box-align: start;
        box-align: start;
        -webkit-align-items: flex-start;
        align-items: flex-start;
        background-color: #fafafa;
        border-bottom: 1.5px solid rgba(0,0,0,0.12);
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-wrap: nowrap;
        flex-wrap: nowrap;
        overflow: visible;
        padding: 10px 30px;
        color: rgba(0,0,0,0.54);
        div{
            color: rgba(0,0,0,0.54);
        }
    }
    .content{
        background: #fafafa;
        -webkit-box-flex: 1 1 auto;
        -webkit-flex: 1 1 auto;
        flex: 1 1 auto;
        overflow: auto;
        position: relative;
        .c1{
            margin-bottom: 100px; 
            margin-top: 2%;
            padding: 0 2%;
            @include breakpoint-max($bp-small) {
                padding: 0;
            }
            padding-bottom: 32px;
            .c2{
                background-color: #fff;
                padding: 0;
                margin-bottom: 50px;
                padding-bottom: 0px;
                margin: 24px 0;
                overflow: visible;
                display: block;
                -webkit-border-radius: 2px;
                border-radius: 2px;
                margin: 10px 0;
                -webkit-box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
                box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
                position: relative;
                text-align: start;
                overflow: hidden;
                -webkit-transition-property: transform,margin,padding,height,opacity;
                transition-property: transform,margin,padding,height,opacity;
                -webkit-transition-delay: 0ms,100ms,0ms,0ms,0ms;
                transition-delay: 0ms,100ms,0ms,0ms,0ms;
                -webkit-transition-duration: 200ms,100ms,100ms,150ms,200ms;
                transition-duration: 200ms,100ms,100ms,150ms,200ms;

                
            }
        }
    }
}
@each $name, $size in $container-sizes {
    .container-#{$name} {
        max-width: $size;
    }
}
