// TABLES
// ==============================

$table-border-color: $color-gray-200 !default;
$table-header-text-color: $color-gray-500 !default;

table {
    min-width: 350px;
    max-width: 500px;
    width: 100%;
    border-spacing: 0;
    border-collapse: collapse;

    tr {
        border-bottom: 1px solid rgba(0,0,0,0.12);
        text-align: left;
        &:last-child{
            border-bottom:none;
        }
    }
    tr td:nth-child(0n+1){
        color: rgba(0,0,0,0.54) !important;
        font-size:14px !important;
        font-weight:500 !important;
        max-width: 10% !important;
        text-align: center !important;
        
    }

    tbody {
        td {
            padding: 5px;
            @include breakpoint-min($bp-small) {
                font-size: 20px;
                font-weight: 600;
            }
            font-size: 18px;
            font-weight: 500;
            text-align: center;
            vertical-align: middle;
            border-right: 1px solid rgba(0,0,0,0.12);
            &:last-child{
                border-right:none;
            }
        }
    }
    tr:nth-child(0n+2) td{
        color: rgba(0,51,204,0.87);
        font-size:16px;
        font-weight:600;
        padding: 10px 2px;
        text-align: center;
        word-spacing:-1px;
        letter-spacing: -1px;
    }
    .showngay div{
        font-size: 23px;
        &:last-child{
            font-size:18px;
            text-align:right;
        }
        
    }
    &.mn{
        border:2px solid #0f9d58;
        @include breakpoint-max($bp-small) {border:none}
        tr th{
            background: #0f9d58;
            border:none;
            color: #eee;
            font-size: 20px;
            font-weight: 700;
            padding: 5px 10px;
        }
        tr:nth-child(0n+3) td{
            color: maroon;
            font-size: 30px;
            font-weight: 600;
        }
        tr:nth-child(0n+11) td{
            color: maroon;
            font-size: 20px;
            font-weight: 600;
        }
    }
    &.mt{
        border:2px solid #f4b400;
        @include breakpoint-max($bp-small) {border:none}
        tr th{
            background: #f4b400;
            border:none;
            color: #eee;
            font-size: 20px;
            font-weight: 700;
            padding: 5px 10px;
        }
        tr:nth-child(0n+3) td{
            color: maroon;
            font-size: 30px;
            font-weight: 600;
        }
        tr:nth-child(0n+11) td{
            color: maroon;
            font-size: 20px;
            font-weight: 600;
        }
    }
    &.mb{
        border:2px solid #db4437;
        @include breakpoint-max($bp-small) {border:none}
        tr th{
            background: #db4437;
            border:none;
            color: #eee;
            font-size: 20px;
            font-weight: 700;
            padding: 5px 10px;
        }
        tr:nth-child(0n+3) td{
            color: maroon;
            font-size: 30px;
            font-weight: 600;
        }
        tr:nth-child(0n+10) td{
            color: maroon;
            font-size: 25px;
            font-weight: 600;
        }
        tr td:last-child{
            display: flex;
            justify-content: center;
            div{
                flex-grow: 1
            }
        }
    }
}
